import { Manage } from "../controller/Manage";
import { debounce, deepClone, sleep, TSleep } from "../util/lib";
import { A, E, T } from "../util/constant";

interface IRunnerMem {
  /**
   * Holds current state of the runner.
   */
  state: E.RUNNER_STATE_BUSY | E.RUNNER_STATE_IDLE;
  /**
   * Instance of sleep function currently triggered.
   */
  delayActionRef: TSleep;
  /**
   * Holds timestamp in milliseconds of current
   * running DELAY_BY action.
   */
  delayStart: number;
  /**
   * Holds timestamp in milliseconds of when pause
   * was triggered.
   */
  pauseStart: number;
  /**
   * Holds a copy of the top to allow mutations.
   */
  cloneTop: IRunnerTop;
  /**
   * Holds a copy of the stack to allow mutations.
   */
  cloneStack: IRunnerTop[];
  /**
   * Holds an instance of the engine's shadow root element.
   */
  host: ShadowRoot["host"];
}

interface IRunnerTop {
  type:
    | E.RUNNER_TYPE_TIMER
    | E.RUNNER_TYPE_INTERACTION
    | E.RUNNER_TYPE_IMMEDIATE;
  detail: number | keyof typeof T;
  actions: [keyof typeof A];
}

interface IRunnerProps {
  uid: string;
  stack: IRunnerTop[];
  aside: IRunnerTop[];
  mem: IRunnerMem;
}

export class Runner extends Manage {
  protected mem: IRunnerProps["mem"] = null;
  private stack: IRunnerProps["stack"];
  private aside: IRunnerProps["aside"];

  constructor({ stack, aside, uid }: Partial<IRunnerProps> = {}) {
    super(uid);
    this.stack = stack || [];
    this.aside = aside || [];
    this.ORM = E.ORM_RUNNERS;
    this._handleExec = this._handleExec.bind(this);
    this.seek = debounce(this.seek, 500);
  }

  modify({ stack, aside }: Partial<IRunnerProps> = {}) {
    this.stack = stack || this.stack;
    this.aside = aside || this.aside;
    return this.put<Runner>({ uid: this.uid });
  }

  register() {
    return this.post<Runner>();
  }

  getAllMem() {
    return this.mem;
  }

  getMem<T = any>(key: keyof IRunnerMem) {
    return this.mem[key] as unknown as T;
  }

  setMem(value: Partial<IRunnerMem>) {
    this.mem = { ...this.mem, ...value };
  }

  bind(shadowRoot: ShadowRoot) {
    const cloneStack = deepClone<IRunnerTop[]>(this.stack);
    const cloneTop = cloneStack.pop();
    this.setMem({
      state: E.RUNNER_STATE_IDLE,
      cloneTop,
      cloneStack,
      host: shadowRoot.host,
    });
    this.getMem("host").addEventListener(
      E.EVENT_ENGINE_TIMEUPDATE,
      this._handleExec
    );
    this.getMem("host").addEventListener(
      E.EVENT_ENGINE_VISUAL_INTERACTION,
      this._handleExec
    );
    return this;
  }

  unbind() {
    this.getMem("host").removeEventListener(
      E.EVENT_ENGINE_TIMEUPDATE,
      this._handleExec
    );
    this.getMem("host").removeEventListener(
      E.EVENT_ENGINE_VISUAL_INTERACTION,
      this._handleExec
    );
    return this;
  }

  /**
   * Adjusts the execution stack according to
   * the `currentTime` of the engine
   */
  seek() {
    this.pause().then(() => {
      const currentTime = this.getMem("host")._currentTime_;
      const filteredStack = this.stack.filter(
        (item) => typeof item.detail === "number" && item.detail >= currentTime
      );
      const cloneStack = deepClone<IRunnerTop[]>(filteredStack);
      this.setMem({ cloneStack, pauseStart: null });
      this._setTop();
    });
  }

  /**
   * Stops the execution of actions
   * and saves the state to allow for
   * resuming.
   */
  pause() {
    return new Promise<void>((resolve) => {
      if (this.getMem("delayActionRef")) {
        this.getMem<TSleep>("delayActionRef").abort();
      }
      this.setMem({ pauseStart: Date.now() });
      resolve();
    });
  }

  /**
   * Resumes execution of the stack after an engine
   * pause event was called
   *
   */
  resume() {
    if (this.getMem("cloneTop")) {
      const remainingDelay =
        this.getMem("pauseStart") - this.getMem("delayStart");
      const topActions = this.getMem<IRunnerTop>("cloneTop").actions;
      const lastExecTopAction = topActions[topActions.length - 1];
      this.setMem({ pauseStart: null });
      if (lastExecTopAction === A.DELAY_BY) {
        topActions[topActions.length - 1] =
          `${A.DELAY_BY}::${remainingDelay}` as any;
      }
      if (this.getMem("state") === E.RUNNER_STATE_BUSY) {
        this._exec();
      }
    } else {
      this.setMem({ pauseStart: null });
    }
  }

  /**
   * Handles matching calls to execute a stack item to the current
   * top item if the top item's detail field's value matches any
   * of the callers `CustomEvent.detail` entries, `_exec` function
   * is run.
   *
   * @param CustomEvent Custom event object having a `detail` property
   */
  private _handleExec({ detail }: CustomEvent) {
    if (
      !this.getMem("host")._isExperiencePaused_ &&
      this.getMem("cloneTop") &&
      this.getMem("state") === E.RUNNER_STATE_IDLE
    ) {
      const { currentTime, eventType, asideIndex } = detail || {
        currentTime: this.getMem("host")._currentTime_,
      };
      switch (this.getMem("cloneTop").type) {
        case E.RUNNER_TYPE_IMMEDIATE:
          this._exec();
          break;
        case E.RUNNER_TYPE_TIMER:
          if (
            this.getMem("cloneTop").detail === Math.floor(currentTime as number)
          ) {
            this._exec();
          }
          break;
        case E.RUNNER_TYPE_INTERACTION:
          if (T[this.getMem("cloneTop").detail] === eventType) {
            this._exec(asideIndex);
          }
          break;
        default:
          console.warn(
            `Undefined:\n${this.getMem("cloneTop").type} is not known`
          );
          break;
      }
    }
  }

  /**
   * Set the top item after the current one's actions are exhausted
   * Also put the runner in an idle state to allow it receive new
   * calls.
   */
  private _setTop() {
    const cloneTop = this.getMem("cloneStack").pop();
    this.setMem({
      cloneTop,
      state: cloneTop ? E.RUNNER_STATE_IDLE : E.RUNNER_STATE_BUSY,
    });
    if (cloneTop && this.getMem("cloneTop").type === E.RUNNER_TYPE_IMMEDIATE) {
      const topCondition = this.getMem("cloneTop")
        .detail as IRunnerTop["detail"];
      const detail =
        typeof topCondition === "number"
          ? topCondition
          : T[this.getMem("cloneTop").detail];
      this._handleExec(
        new CustomEvent(null, {
          detail: { detail },
        })
      );
    }
  }

  /**
   * Runs all instructions in the top.actions stack.
   * The execution is stop when there is a pause signal
   * from the engine.
   *
   * @param asideIndex
   * @returns void
   */
  private async _exec(asideIndex?: number) {
    this.setMem({ state: E.RUNNER_STATE_BUSY });
    const topActions = this.getMem<IRunnerTop>("cloneTop").actions;
    while (topActions.length) {
      const [namespace, arg = null] =
        topActions[topActions.length - 1].split("::");
      const name = A[namespace];
      switch (name) {
        case A.DELAY_BY:
          this.setMem({
            delayActionRef: sleep(
              (1000 * parseInt(arg)) / this.getMem("host")._playbackRate_
            ),
            delayStart: Date.now(),
          });
          try {
            await this.getMem<TSleep>("delayActionRef").now();
          } catch (error) {
            console.warn(`Interruption:\n${error} for ${namespace}`);
          }
          break;
        case A.PUSH_ASIDE:
          const idx = arg || asideIndex;
          if (this.aside[idx]) {
            this.getMem("cloneStack").push(
              deepClone<IRunnerTop>(this.aside[idx])
            );
          } else {
            console.warn(`Undefined:\naside index ${idx} is out of bounds`);
          }
          break;
        default:
          if (this.getMem("host")[name]) {
            this.getMem("host")[name](arg);
          } else {
            console.warn(`Undefined:\n${name} is NOT a valid action`);
          }
          break;
      }
      if (this.getMem("pauseStart")) return;
      topActions.pop();
    }
    this._setTop();
  }
}
