import { Host } from "./Host";
import { C } from "../util/constant";

interface IPointer {
  [key: string]: string;
}

export class Manage extends Host {
  constructor(uid: string) {
    super(uid);
  }

  protected post<T>() {
    return new Promise<T>((resolve, reject) => {
      this.fetch({
        body: this.serialize(this),
        endpoint: C.HOST_ENDPOINT_POST + this.parseQuery({ table: this.ORM }),
        method: "POST",
      })
        .then((dto) => resolve(this.deserialize<T>(dto)))
        .catch((error) => reject(error));
    });
  }

  protected put<T>(pointer: IPointer) {
    return new Promise<T>((resolve, reject) => {
      this.fetch({
        body: this.serialize(this),
        endpoint:
          C.HOST_ENDPOINT_PUT +
          this.parseQuery({ ...pointer, table: this.ORM }),
        method: "PUT",
      })
        .then((dto) => resolve(this.deserialize<T>(dto)))
        .catch((error) => reject(error));
    });
  }

  get<T>(pointer: IPointer) {
    return new Promise<T>((resolve, reject) => {
      this.fetch({
        endpoint:
          C.HOST_ENDPOINT_GET +
          this.parseQuery({ ...pointer, table: this.ORM }),
        method: "GET",
      })
        .then((dto) => resolve(this.deserialize<T>(dto)))
        .catch((error) => reject(error));
    });
  }

  delete(pointer: IPointer) {
    return new Promise<boolean>((resolve, reject) => {
      this.fetch({
        endpoint:
          C.HOST_ENDPOINT_DELETE +
          this.parseQuery({ ...pointer, table: this.ORM }),
        method: "DELETE",
      })
        .then(() => resolve(true))
        .catch((error) => reject(error));
    });
  }
}
