import { Video } from "./Video";
import { rootStyles } from "../util/misc";
import { E } from "../util/constant";

interface IEngineProps {
  styleElement: HTMLStyleElement;
  externalStyleElement: HTMLStyleElement;
  externalScriptElement: HTMLScriptElement;
}

export class Engine extends Video {
  private styleElement: IEngineProps["styleElement"];
  private externalStyleElement: IEngineProps["externalStyleElement"];
  private externalScriptElement: IEngineProps["externalScriptElement"];

  constructor({}: Partial<IEngineProps> = {}) {
    super();
  }

  connectedCallback() {
    this.onEngineReady(() => {
      this.styleElement = this._addStyleElement();
      this.externalStyleElement = this._addExternalStyleElement();
      this.externalScriptElement = this._addExternalScriptElement();
      this.root.append(
        this.styleElement,
        this.externalStyleElement,
        this.externalScriptElement
      );
      const container = this._addEngineChildren();
      this.root.append(container);
    });
  }

  attributeChangedCallback(name: string, _oldValue, _newValue) {
    let oldNode, newNode;
    if (name === "data") {
      this.onDataChange(() => {
        this._resetEngineChildren();
      });
      return;
    }
    if (name === "run") {
      this.runner = this.runners[this.run];
      return;
    }
    if (name === "conf") {
      this.config = this.configs[this.conf];
      this._addVideoSrc();
      this._addAudioSrc();
      return;
    }
    if (this.isConnected && this.configs && this.root.childNodes.length) {
      switch (name) {
        case "css":
          oldNode = this.externalStyleElement;
          newNode = this._addExternalStyleElement();
          this.root.replaceChild(newNode, oldNode);
          this.externalStyleElement = newNode;
          break;
        case "script":
          oldNode = this.externalScriptElement;
          newNode = this._addExternalScriptElement();
          this.root.replaceChild(newNode, oldNode);
          this.externalScriptElement = newNode;
          break;
        default:
          break;
      }
      return;
    }
  }

  disconnectedCallback() {
    this.onEngineDestroyed(() => {
      console.info("Engine element removed from page.");
    });
  }

  adoptedCallback() {
    console.info("Engine element moved to new document.");
  }

  private _addStyleElement() {
    const style = document.createElement("style");
    style.textContent = rootStyles;
    return style;
  }

  private _addExternalStyleElement() {
    const style = document.createElement("style");
    if (this.css) {
      style.textContent = `@import url('${this.css}');`;
    }
    return style;
  }

  private _addExternalScriptElement() {
    const script = document.createElement("script");
    if (this.script) {
      script.src = this.script;
      script.async = true;
    }
    return script;
  }

  private _addEngineChildren() {
    const container = document.createElement("figure");
    container.id = E.CSS_ENGINE_CONTAINER;
    container.append(
      this._addVideoElement(),
      this._addAudioElement(),
      this._addVisualElement(),
      this._addControlElement(),
      this._addLoaderElement()
    );
    return container;
  }

  protected _resetEngineChildren() {
    const container = this.root.getElementById(E.CSS_ENGINE_CONTAINER);
    const children = [
      this._addVideoElement(),
      this._addAudioElement(),
      this._addVisualElement(),
      this._addControlElement(),
      this._addLoaderElement(),
    ];
    try {
      container.replaceChildren(...children);
    } catch (error) {
      container.innerHTML = "";
      container.append(...children);
      console.warn(
        `Compatibility:\nreplaceChildren not supported by browser\n\n${error}`
      );
    }
  }
}
