import { Manage } from "../controller/Manage";
import { E } from "../util/constant";

interface ISessionProps {
  uid: string;
}

export class Session extends Manage {
  constructor({ uid }: Partial<ISessionProps> = {}) {
    super(uid);

    this.ORM = E.ORM_SESSIONS;
  }

  modify({}: Partial<ISessionProps> = {}) {
    return this.put<Session>({ uid: this.uid });
  }

  register() {
    return this.post<Session>();
  }
}
