import { E } from "./util/constant";
import { Engine } from "./view/Engine";

function on(id: string) {
  return new Promise<Engine>((resolve, reject) => {
    customElements.define("interactive-video-engine", Engine);
    const iveElement = document.getElementById(id) as Engine;
    if (iveElement) {
      iveElement.addEventListener(E.EVENT_ENGINE_READY, () => {
        resolve(iveElement);
      });
    } else {
      reject("Missing:\nNo element found in DOM with this ID");
    }
  });
}

export { on };
