import { C, E } from "../util/constant";
import { ObjectMapper, JsonIgnore } from "../util/vendor";

export interface IHostProps {
  ORM: E.ORM_CONFIGS | E.ORM_RUNNERS | E.ORM_SESSIONS;
  uid: string;
}

interface IFetchProps {
  body: String;
  endpoint: string;
  method: "POST" | "GET" | "PUT" | "DELETE";
}

export class Host {
  // @JsonIgnore()
  protected mem;
  protected ORM: IHostProps["ORM"];
  protected uid: IHostProps["uid"];

  constructor(uid?: IHostProps["uid"]) {
    this.uid = uid || Math.random().toString(16).slice(2);
  }

  protected deserializeArray<T>(dtoList: Object[]) {
    return ObjectMapper.deserializeArray<T>(this.constructor as any, dtoList);
  }

  protected deserialize<T>(dto: Object) {
    return ObjectMapper.deserialize<T>(this.constructor as any, dto);
  }

  protected serialize(cls: Object) {
    return ObjectMapper.serialize(cls);
  }

  protected parseQuery(arg = {}) {
    return new URLSearchParams({ ...arg });
  }

  protected fetch(arg: Partial<IFetchProps>) {
    switch (C.DEFAULT_HOST_TYPE) {
      case E.HOST_TYPE_EXTERNAL:
        return this._external(arg);
      case E.HOST_TYPE_INTERNAL:
        return this._internal(arg);
      default:
        return Promise.reject(
          "NOT FOUND:\nHost type not found. Please set DEFAULT_HOST_TYPE to correct value"
        );
    }
  }

  private _external(arg) {
    const { body, endpoint, method } = arg || {};
    return new Promise<any>((resolve, reject) => {
      fetch(endpoint, {
        method,
        headers: {
          "Content-Type": "application/json",
        },
        ...(body ? { body } : {}),
      })
        .then((response) => response.json())
        .then((data) => resolve(data))
        .catch((error) => reject(error));
    });
  }

  private _internal(arg) {
    const { body, endpoint } = arg || {};
    let pointer = [];
    const { pathname, searchParams } = new URL(`${C.HOST_BASE_URL}${endpoint}`);
    const dbTable = searchParams.get("table");
    for (const [key, value] of searchParams) {
      if (key != "table") {
        pointer = [key, value];
      }
    }
    return new Promise<any>((resolve, reject) => {
      switch (pathname + "?") {
        case C.HOST_ENDPOINT_GET_ALL:
          resolve(C.DB[dbTable]);
          break;

        case C.HOST_ENDPOINT_GET:
          resolve(
            C.DB[dbTable].filter((entry) => entry[pointer[0]] === pointer[1])[0]
          );
          break;

        case C.HOST_ENDPOINT_POST:
          C.DB[dbTable].push(JSON.parse(body));
          resolve(JSON.parse(body));
          break;

        case C.HOST_ENDPOINT_PUT:
          let res;
          C.DB[dbTable] = C.DB[dbTable].map((entry) => {
            if (entry[pointer[0]] === pointer[1]) {
              res = { ...entry, ...JSON.parse(body) };
              return res;
            } else {
              return entry;
            }
          });
          resolve(res);
          break;

        case C.HOST_ENDPOINT_DELETE:
          C.DB[dbTable] = C.DB[dbTable].filter(
            (entry) => entry[pointer[0]] !== pointer[1]
          );
          resolve("{status: SUCCESSFUL}");
          break;

        default:
          reject(`Unknown api endpoint ${pathname}`);
          break;
      }
    });
  }
}
