import { Audio } from "./Audio";
import { E } from "../util/constant";

interface IVideoProps {}

export class Video extends Audio {
  constructor({}: Partial<IVideoProps> = {}) {
    super();
  }

  protected _addVideoElement() {
    const video = this._createVideo();
    video.append(...this._createVideoTrack(), this._createNoVideoSupport());
    this._addVideoEventListener(video);
    this._addVideoSrc(video);
    return video;
  }

  protected _addVideoSrc(video?: HTMLVideoElement) {
    const element =
      video ||
      (this.root.getElementById(E.CSS_VIDEO_ELEMENT) as HTMLVideoElement);
    element.poster = this.config.film.poster;
    this.dispatchEvent(
      new CustomEvent(E.EVENT_ENGINE_ATTACH_VIDEO_SRC, {
        detail: { element, member: "film" },
      })
    );
  }

  private _createVideo() {
    const video = document.createElement("video");
    video.id = E.CSS_VIDEO_ELEMENT;
    video.disablePictureInPicture = true;
    video.disableRemotePlayback = true;
    video.playsInline = true;
    video.autoplay = false;
    video.controls = false;
    video.muted = true;
    video.loop = false;
    video.preload = "metadata";
    video.ontouchend = this._toggleControlVisibilityOnTouchDevice_.bind(this);
    return video;
  }

  private _createVideoTrack() {
    const nodes: HTMLTrackElement[] = [];
    for (const { label, src } of this.config.film.tracks) {
      const trackElement = document.createElement("track");
      trackElement.kind = "captions";
      trackElement.label = label;
      trackElement.src = src;
      trackElement.default = false;
      trackElement.srclang = label.substring(0, 2).toLowerCase();
      trackElement.classList.add(E.CSS_CLS_HIDDEN);
      nodes.push(trackElement);
    }
    return nodes;
  }

  private _createNoVideoSupport() {
    return document.createTextNode("HTML5 video not supported by browser");
  }

  /**
   * Sets needed listeners on HTML video element
   * and reacts as needed.
   *
   * @param video
   */
  private _addVideoEventListener(video: HTMLVideoElement) {
    video.onprogress = (_event) => {
      const duration = video.duration;
      const buffer = video.buffered;
      for (let i = 1; i <= buffer.length; i++) {
        const start = buffer.start(buffer.length - i);
        const end = buffer.end(buffer.length - i);
        if (start < video.currentTime) {
          this._filmBuffered_ = end / duration;
          break;
        }
      }
      if (this._filmBuffered_ > 0.75) {
        this._filmLoadProgress_ = 1;
      } else {
        this._filmLoadProgress_ =
          this._filmLoadProgress_ + (1 - this._filmLoadProgress_) * 0.1;
      }
    };
    video.oncanplaythrough = () => {
      this._filmLoadProgress_ = 1;
      this._filmBuffered_ = 1;
    };
    video.onwaiting = () => {
      this._isBuffering_ = true;
    };
    video.onplaying = () => {
      this._isBuffering_ = false;
    };
    video.onerror = (error) => {
      this._isFilmLoadError_ = true;
      console.warn(`PlayVideoError:\naudio.onerror call\n\n${error}`);
    };
    video.ontimeupdate = (_event) => {
      this.dispatchEvent(new CustomEvent(E.EVENT_ENGINE_TIMEUPDATE));
    };
  }
}
