import { Host, IHostProps } from "./Host";
import { Config } from "../model/Config";
import { Runner } from "../model/Runner";
import { Session } from "../model/Session";
import { E, C } from "../util/constant";

const CLS = {
  [E.ORM_CONFIGS]: Config,
  [E.ORM_RUNNERS]: Runner,
  [E.ORM_SESSIONS]: Session,
};

export class Collection extends Host {
  constructor(dbTable: IHostProps["ORM"]) {
    super();
    this.ORM = dbTable;
    this.constructor = CLS[dbTable];
  }

  then<T>(func: (arg: T[]) => void) {
    return new Promise<void>((resolve, reject) => {
      this.fetch({
        endpoint:
          C.HOST_ENDPOINT_GET_ALL + this.parseQuery({ table: this.ORM }),
        method: "GET",
      })
        .then((dtoList) => this.deserializeArray<T>(dtoList))
        .then((list) => resolve(func(list)))
        .catch((error) => reject(error));
    });
  }
}
