import { Core } from "./Core";
import { C, E } from "../util/constant";

interface ILoaderProps {}

export class Loader extends Core {
  constructor({}: Partial<ILoaderProps> = {}) {
    super();
  }

  protected _addLoaderElement() {
    const loader = this._createLoader();
    loader.append(
      this._createWelcomeGroup(), // z-index 2
      this._createNetworkErrorGroup(), // z-index 2
      this._createVideoLoadProgressGroup(), // z-index 3
      this._createEngineBusySpinner(), // z-index 3
      this._createVideoCaptionContainer(), // z-index 0
      this._createAudioCaptionContainer(), // z-index 0
      this._createResumePlayBtn() // z-index 1
    );
    this._addLoaderListener(loader);
    return loader;
  }

  private _createLoader() {
    const container = document.createElement("div");
    container.id = E.CSS_LOADER_CONTAINER;
    return container;
  }

  private _createWelcomeGroup() {
    const wrapper = document.createElement("span");
    const text = document.createElement("span");
    const btn = document.createElement("button");
    wrapper.id = E.CSS_LOADER_WELCOME_GROUP;
    text.innerHTML = `${C.WELCOME_CTA_MSG}${C.POWERED_BY_IVE_MSG}`;
    btn.classList.add(E.CSS_CLS_ICON_OK_BG);
    btn.ariaLabel = "okay";
    btn.disabled = true;
    btn.onclick = this._initExperience_.bind(this);
    wrapper.append(text, btn);
    return wrapper;
  }

  private _createNetworkErrorGroup() {
    const wrapper = document.createElement("span");
    const text = document.createElement("span");
    const btn = document.createElement("button");
    wrapper.id = E.CSS_LOADER_NETWORK_ERROR_GROUP;
    wrapper.classList.add(E.CSS_CLS_HIDDEN);
    text.innerHTML = `${C.NETWORK_ERROR_CTA_MSG}`;
    btn.classList.add(E.CSS_CLS_ICON_RELOAD_BG);
    btn.ariaLabel = "reload";
    btn.onclick = () => location.reload();
    wrapper.append(text, btn);
    return wrapper;
  }

  private _createVideoLoadProgressGroup() {
    const wrapper = document.createElement("span");
    const count = document.createTextNode("0%");
    const progressBar = document.createElement("progress");
    wrapper.id = E.CSS_LOADER_VIDEO_LOAD_PROGRESS_GROUP;
    wrapper.classList.add(E.CSS_CLS_HIDDEN);
    progressBar.ariaLabel = "video loading";
    progressBar.value = 0;
    progressBar.max = 100;
    wrapper.append(count, progressBar);
    return wrapper;
  }

  private _createEngineBusySpinner() {
    const node = document.createElement("span");
    node.id = E.CSS_LOADER_ENGINE_BUSY_GROUP;
    node.classList.add(E.CSS_CLS_HIDDEN);
    return node;
  }

  private _createVideoCaptionContainer() {
    const node = document.createElement("span");
    node.id = E.CSS_LOADER_VIDEO_CAPTION_CONTAINER;
    node.classList.add(E.CSS_CLS_HIDDEN);
    return node;
  }

  private _createAudioCaptionContainer() {
    const node = document.createElement("span");
    node.id = E.CSS_LOADER_AUDIO_CAPTION_CONTAINER;
    node.classList.add(E.CSS_CLS_HIDDEN);
    return node;
  }

  private _createResumePlayBtn() {
    const btn = document.createElement("button");
    btn.id = E.CSS_LOADER_RESUME_PLAY_BTN;
    btn.ariaLabel = "resume experience";
    btn.classList.add(E.CSS_CLS_HIDDEN, E.CSS_CLS_ICON_RESUME_PLAY_BG);
    btn.onclick = this._toggleExperiencePlay_.bind(this);
    return btn;
  }

  private _addLoaderListener(loader: HTMLElement) {
    const [welcomeCTAgroup, networkErrorGroup, progressGroup, busySpinner] =
      loader.childNodes as any;
    const [, okayBtn] = welcomeCTAgroup.childNodes as any;
    this.addEventListener(E.EVENT_ENGINE_EXPERIENCE_LOADING, () => {
      const [count, progress] = progressGroup.childNodes as any;
      if (this._isFilmLoadError_ || this._isAudioLoadError_) {
        welcomeCTAgroup.hideMe();
        progressGroup.hideMe();
        busySpinner.hideMe();
        (<any>this.root.getElementById(E.CSS_CONTROL_CONTAINER)).hideMe();
        networkErrorGroup.showMe();
        return;
      }
      if ((this._filmLoadProgress_ + this._audioLoadProgress_) / 2 < 0.85) {
        progressGroup.showMe();
        const percentage = Math.round(
          ((this._filmLoadProgress_ + this._audioLoadProgress_) / 2) * 100
        );
        count.nodeValue = `${percentage}%`;
        progress.value = percentage;
        progress.innerHTML = `${percentage}% loaded`;
      } else {
        okayBtn.disabled = false;
        progressGroup.hideMe();
      }
      if (this._isBuffering_) {
        busySpinner.showMe();
      } else {
        busySpinner.hideMe();
      }
    });
  }
}
