import type { Config } from "../model/Config";
import type { Runner } from "../model/Runner";
import { Handle } from "./Handle";
import { Collection } from "../controller/Collection";
import { C, E } from "../util/constant";

interface ICoreProps {}

export class Core extends Handle {
  constructor({}: Partial<ICoreProps> = {}) {
    super();
  }

  protected onEngineReady(callback: Function) {
    this._loadDataSrc().then(() => {
      callback.apply(this);
      this._extendElementPrototype();
      this.dispatchEvent(new CustomEvent(E.EVENT_ENGINE_READY));
    });
  }

  protected onDataChange(callback: Function) {
    if (this.isConnected && this.configs && this.root.childNodes.length) {
      this._loadDataSrc().then(() => {
        callback.apply(this);
      });
    }
  }

  protected onEngineDestroyed(callback: Function) {
    this._shrinkElementPrototype();
    callback.apply(this);
  }

  private _loadDataSrc() {
    return new Promise<void>((resolve, reject) => {
      fetch(this.data)
        .then((response) => response.json())
        .then((data) => C.update("DB", data))
        .then(() => {
          new Collection(E.ORM_CONFIGS)
            .then<Config>((configs) => {
              this.configs = configs;
              this.config = this.configs[this.conf];
            })
            .then(() => {
              new Collection(E.ORM_RUNNERS)
                .then<Runner>((runners) => {
                  this.runners = runners;
                  this.runner = this.runners[this.run];
                  resolve();
                })
                .catch((error) => reject(error));
            })
            .catch((error) => reject(error));
        });
    });
  }
}
