import { Manage } from "../controller/Manage";
import { E } from "../util/constant";

interface IConfigMem {
  /**
   * Holds an instance of the engine's shadow root element
   */
  host: ShadowRoot["host"];
}

interface IConfigFilm {
  src: string;
  type: string;
  poster: string;
  tracks: { label: string; src: string }[];
  chapters: number[];
}

interface IConfigAudio {
  src: string;
  type: string;
  tracks: { label: string; src: string }[];
}

interface IConfigVisual {
  uid: string;
  elements: {
    tag: keyof HTMLElementTagNameMap;
    id: string;
    text: string;
    classList: string[];
    attribute: { [key: string]: string };
    event: { [key: string]: string };
    children: IConfigVisual[];
  }[];
  riots: { tag: string; id: string }[];
}

interface IConfigProps {
  uid: string;
  film: IConfigFilm;
  audio: IConfigAudio;
  visuals: IConfigVisual[];
  mem: IConfigMem;
}
export class Config extends Manage {
  protected mem: IConfigProps["mem"] = null;
  public film: IConfigProps["film"];
  public audio: IConfigProps["audio"];
  public visuals: IConfigProps["visuals"];

  constructor({ film, audio, visuals, uid }: Partial<IConfigProps> = {}) {
    super(uid);
    this.film = film;
    this.audio = audio;
    this.visuals = visuals;
    this.ORM = E.ORM_CONFIGS;
    this._handleSourceLoad = this._handleSourceLoad.bind(this);
  }

  modify({ film, audio, visuals }: Partial<IConfigProps> = {}) {
    this.film = film || this.film;
    this.audio = audio || this.audio;
    this.visuals = visuals || this.visuals;
    return this.put<Config>({ uid: this.uid });
  }

  register() {
    return this.post<Config>();
  }

  getAllMem() {
    return this.mem;
  }

  getMem<T = any>(key: keyof IConfigProps["mem"]) {
    return this.mem[key] as unknown as T;
  }

  setMem(value: Partial<IConfigProps["mem"]>) {
    this.mem = { ...this.mem, ...value };
  }

  bind(shadowRoot: ShadowRoot) {
    this.setMem({
      host: shadowRoot.host,
    });
    this.getMem("host").addEventListener(
      E.EVENT_ENGINE_ATTACH_VIDEO_SRC,
      this._handleSourceLoad
    );
    this.getMem("host").addEventListener(
      E.EVENT_ENGINE_ATTACH_AUDIO_SRC,
      this._handleSourceLoad
    );
    return this;
  }

  unbind() {
    this.getMem("host").removeEventListener(
      E.EVENT_ENGINE_ATTACH_VIDEO_SRC,
      this._handleSourceLoad
    );
    this.getMem("host").removeEventListener(
      E.EVENT_ENGINE_ATTACH_AUDIO_SRC,
      this._handleSourceLoad
    );
    return this;
  }

  /**
   * Handles attaching src data to video/audio element.
   * Attempts to use Media source extension strategy as default
   * and falls back to HTML5 standard with a URL blob object as src.
   *
   * @param event
   */
  private _handleSourceLoad({ detail }: CustomEventInit) {
    const { member, element } = detail || {};
    const mimeTypeXCodecs = this[member].type;
    const [mimeType, _codecs] = mimeTypeXCodecs.split(";");
    URL.revokeObjectURL(element.src);
    if (
      "MediaSource" in window &&
      MediaSource.isTypeSupported(mimeTypeXCodecs)
    ) {
      const source = new MediaSource();
      element.src = URL.createObjectURL(source);
      source.onsourceopen = () => {
        const buffer = source.addSourceBuffer(mimeTypeXCodecs);
        this._fetch(member)
          .then((arraybuffer) => {
            buffer.onupdateend = () => {
              try {
                source.endOfStream();
              } catch (error) {
                URL.revokeObjectURL(element.src);
                element.src = URL.createObjectURL(
                  new Blob([arraybuffer], { type: mimeType })
                );
                console.warn(
                  `Unknown:\nPossibly unsupported media type codecs or un-fragmented video source\n\n${error}`
                );
              }
            };
            return arraybuffer;
          })
          .then((arraybuffer) => {
            buffer.appendBuffer(arraybuffer);
          })
          .catch((error) => {
            URL.revokeObjectURL(element.src);
            element.src = this[member].src;
            console.warn(error);
          })
          .finally(() => {
            element.load();
          });
      };
    } else {
      element.src = this[member].src;
      element.load();
      console.warn(
        `Unsupported:\n${this[member].type} mime type\nMedia source extension is not available in browser\nfalling back to HTML5 standard src url.`
      );
    }
  }

  /**
   * Handle XMLHttpRequest to get media data from server.
   *
   * @param member string identifying field i.e. (film | audio)
   * @returns promise which resolves to an buffer
   */
  private _fetch(member: string) {
    let isTooLarge = false;
    return new Promise<BufferSource>((resolve, reject) => {
      const req = new XMLHttpRequest();
      req.responseType = "arraybuffer";
      req.open("GET", this[member].src);
      req.onprogress = (event) => {
        if (isTooLarge) {
          req.abort();
          reject(
            `Memory:\n${member} size exceeds 250mb\nabandoned using blob src`
          );
        }
        if (event.lengthComputable) {
          isTooLarge = event.total > 262144000;
          this.getMem("host")[`_${member}LoadProgress_`] =
            (event.loaded / event.total) * 0.75;
        }
      };
      req.onload = () => resolve(req.response);
      req.onerror = () =>
        reject("Network:\nsomething went wrong during xmlHttpRequest.");
      req.send();
    });
  }
}
