import type { Config } from "../model/Config";
import type { Runner } from "../model/Runner";
import { vendor } from "../util/vendor";
import { E } from "../util/constant";

interface IBaseProps {
  _detail: any;
  _config: Config;
  _configs: Config[];
  _runner: Runner;
  _runners: Runner[];
  _root: ShadowRoot;
}

export class Base extends HTMLElement {
  private _root: IBaseProps["_root"];
  private _detail: IBaseProps["_detail"];
  private _config: IBaseProps["_config"];
  private _configs: IBaseProps["_configs"];
  private _runner: IBaseProps["_runner"];
  private _runners: IBaseProps["_runners"];

  constructor({}: Partial<IBaseProps> = {}) {
    super();
    if (!this.hasAttribute("run")) this.setAttribute("run", "0");
    if (!this.hasAttribute("conf")) this.setAttribute("conf", "0");
    this.root = this.attachShadow({ mode: "closed" });
    // LOGGER
    console.log(
      "%c\n__________\n\n  I V E      \n__________\n",
      `font-size: 25px; ${E.IVE_FONT_LOGO_STYLE}`
    );
  }
  static get observedAttributes() {
    return ["css", "script", "data", "run", "conf"];
  }

  get run() {
    return parseInt(this.getAttribute("run"));
  }
  get conf() {
    return parseInt(this.getAttribute("conf"));
  }
  get css() {
    return this.getAttribute("css");
  }
  get script() {
    return this.getAttribute("script");
  }
  get data() {
    return this.getAttribute("data");
  }

  get detail() {
    return this._detail;
  }

  set detail(obj: IBaseProps["_detail"]) {
    this._detail = obj;
  }

  get runner() {
    return this._runner;
  }

  set runner(inst: IBaseProps["_runner"]) {
    if (this._runner) this._runner.unbind();
    this._runner = inst;
    this._runner.bind(this.root);
  }

  get runners() {
    return this._runners;
  }

  set runners(inst: IBaseProps["_runners"]) {
    this._runners = inst;
  }

  get configs() {
    return this._configs;
  }

  set configs(inst: IBaseProps["_configs"]) {
    this._configs = inst;
  }

  get config() {
    return this._config;
  }

  set config(inst: IBaseProps["_config"]) {
    if (this._config) this._config.unbind();
    this._config = inst;
    this._config.bind(this.root);
  }

  get root() {
    return this._root;
  }

  set root(inst: IBaseProps["_root"]) {
    this._root = inst;
    vendor.bind(this._root);
  }

  protected _extendElementPrototype() {
    Element.prototype["showMe"] = function () {
      this.classList.remove(E.CSS_CLS_HIDDEN);
    };

    Element.prototype["hideMe"] = function () {
      this.classList.add(E.CSS_CLS_HIDDEN);
    };

    Element.prototype["showFocusMe"] = function () {
      this.classList.remove(E.CSS_CLS_HIDDEN);
      this.focus();
    };

    Element.prototype["toggleMe"] = function () {
      this.classList.toggle(E.CSS_CLS_HIDDEN);
    };
  }

  protected _shrinkElementPrototype() {
    delete Element.prototype["showMe"];
    delete Element.prototype["hideMe"];
    delete Element.prototype["showFocusMe"];
    delete Element.prototype["toggleMe"];
  }
}
