/**
 * Makes a deep copy of an object.
 *
 * @param obj
 * @returns copy of the object
 */
export function deepClone<T = any>(obj: object) {
  return JSON.parse(JSON.stringify(obj)) as T;
}

/**
 * Applies only the tail call to a function
 * after multiple repeated calls.
 *
 * @param func
 * @param wait
 * @returns callable function
 */
export function debounce(func: Function, wait: number = 0) {
  let timeout;
  return function (...args) {
    clearTimeout(timeout);
    timeout = setTimeout(() => {
      func.apply(this, args);
    }, wait);
  };
}

const _sleep_ = {
  _wait: 0,
  _timerRef: null,
  _resolve: null,
  _reject: null,
  _prom() {
    return new Promise<void>((resolve, reject) => {
      this._resolve = resolve;
      this._reject = reject;
    });
  },
  now() {
    const prom = this._prom();
    this._timerRef = setTimeout(() => this._resolve(), this._wait);
    return prom;
  },
  abort() {
    clearTimeout(this._timerRef);
    if (this._reject) {
      this._reject("Kill signal received");
    }
  },
};
export type TSleep = typeof _sleep_;
/**
 * When called the returned values allows for two method calls:
 *  - `now` which starts the wait time passed and
 *    returns a promise which resolves when time is exhausted
 *  - `abort` which cancels any previously called wait time and
 *    causes the `now` promise to be rejected
 *
 * @param  wait time in milliseconds
 * @returns this
 * @example
 * const stop = sleep(5000)
 * stop.now().then(doSomethingOnSuccess).catch(doSomethingOnAbortTrigger)
 * // if we wanted to cancel stop from any where in the code
 * stop.abort()
 */
export function sleep(wait: number): typeof _sleep_ {
  const inst = Object.create(_sleep_) as typeof _sleep_;
  inst._wait = wait;
  return inst;
}
