import { Visual } from "./Visual";
import { E } from "../util/constant";

interface IAudioProps {}

export class Audio extends Visual {
  constructor({}: Partial<IAudioProps> = {}) {
    super();
  }

  protected _addAudioElement() {
    const audio = this._createAudio();
    audio.append(...this._createAudioTrack(), this._createNoAudioSupport());
    this._addAudioEventListener(audio);
    this._addAudioSrc(audio);
    return audio;
  }

  protected _addAudioSrc(audio?: HTMLAudioElement) {
    const element =
      audio ||
      (this.root.getElementById(E.CSS_AUDIO_ELEMENT) as HTMLAudioElement);
    this.dispatchEvent(
      new CustomEvent(E.EVENT_ENGINE_ATTACH_AUDIO_SRC, {
        detail: { element, member: "audio" },
      })
    );
  }

  private _createAudio() {
    const audio = document.createElement("audio");
    audio.id = E.CSS_AUDIO_ELEMENT;
    audio.autoplay = false;
    audio.controls = false;
    audio.loop = false;
    audio.muted = true;
    audio.preload = "metadata";
    return audio;
  }

  private _createAudioTrack() {
    const nodes: HTMLTrackElement[] = [];
    for (const { label, src } of this.config.audio.tracks) {
      const trackElement = document.createElement("track");
      trackElement.kind = "captions";
      trackElement.label = label;
      trackElement.src = src;
      trackElement.default = false;
      trackElement.srclang = label.substring(0, 2).toLowerCase();
      trackElement.classList.add(E.CSS_CLS_HIDDEN);
      nodes.push(trackElement);
    }
    return nodes;
  }

  private _createNoAudioSupport() {
    return document.createTextNode("HTML5 audio not supported by browser");
  }

  /**
   * Sets needed listeners on HTML audio element
   * and reacts as needed.
   *
   * @param audio
   */
  private _addAudioEventListener(audio: HTMLAudioElement) {
    audio.onprogress = (_event) => {
      const duration = audio.duration;
      const buffer = audio.buffered;
      for (let i = 1; i <= buffer.length; i++) {
        const start = buffer.start(buffer.length - i);
        const end = buffer.end(buffer.length - i);
        if (start < audio.currentTime) {
          this._audioBuffered_ = end / duration;
          break;
        }
      }
      if (this._audioBuffered_ > 0.75) {
        this._audioLoadProgress_ = 1;
      } else {
        this._audioLoadProgress_ =
          this._audioLoadProgress_ + (1 - this._audioLoadProgress_) * 0.1;
      }
    };
    audio.oncanplaythrough = () => {
      this._audioLoadProgress_ = 1;
      this._audioBuffered_ = 1;
    };
    audio.onwaiting = () => {
      this._isBuffering_ = true;
    };
    audio.onwaiting = () => {
      this._isBuffering_ = false;
    };
    audio.onerror = (error) => {
      this._isAudioLoadError_ = true;
      console.warn(`PlayAudioError:\naudio.onerror call\n\n${error}`);
    };
  }
}
