import { Loader } from "./Loader";
import { E } from "../util/constant";

interface IControlProps {}

export class Control extends Loader {
  constructor({}: Partial<IControlProps> = {}) {
    super();
  }

  protected _addControlElement() {
    const control = this._createControl();
    control.append(
      this._createControlProgressTimeGroup(),
      this._createControlCTAGroup()
    );
    this._addControlListener(control);
    return control;
  }

  private _createControl() {
    const container = document.createElement("aside");
    container.id = E.CSS_CONTROL_CONTAINER;
    container.classList.add(E.CSS_CLS_HIDDEN);
    return container;
  }

  private _createControlProgressTimeGroup() {
    const wrapper = document.createElement("span");
    const progress = document.createElement("span");
    const played = document.createElement("span");
    const buffered = document.createElement("span");
    const range = document.createElement("input");
    const text = document.createElement("span");
    wrapper.id = E.CSS_CONTROL_PROGRESS_TIME_GROUP;
    text.id = E.CSS_CONTROL_TIME_PER_DURATION;
    progress.id = E.CSS_CONTROL_PROGRESS_BAR;
    progress.ariaLabel = "film progress bar";
    progress.tabIndex = 0;
    progress.setAttribute("role", "progressbar");
    progress.setAttribute("aria-valuenow", "0");
    progress.setAttribute("aria-valuemin", "0");
    progress.setAttribute("aria-valuemax", "100");
    progress.setAttribute("aria-valuetext", "0% played");
    range.type = "range";
    range.name = "film range";
    range.min = "0";
    range.max = "1";
    range.step = "0.01";
    range.value = "0";
    range.ariaLabel = "film range slider";
    range.oninput = this._changeCurrentTime_.bind(this);
    progress.append(played, buffered, range);
    wrapper.append(progress, text);
    return wrapper;
  }

  private _createControlPreviousBtnWrapper() {
    const btn = document.createElement("button");
    btn.ariaLabel = "previous experience";
    btn.id = E.CSS_CONTROL_PREVIOUS_BTN;
    btn.classList.add(E.CSS_CLS_ICON_PREV_BG);
    btn.onclick = this._changeExperience_.bind(this);
    return btn;
  }

  private _createControlPlayPauseBtnWrapper() {
    const btn = document.createElement("button");
    btn.ariaLabel = "play/pause";
    btn.id = E.CSS_CONTROL_PLAY_PAUSE_BTN;
    btn.classList.add(E.CSS_CLS_ICON_PLAY_BG);
    btn.onclick = this._toggleExperiencePlay_.bind(this);
    return btn;
  }

  private _createControlNextBtnWrapper() {
    const btn = document.createElement("button");
    btn.ariaLabel = "next experience";
    btn.id = E.CSS_CONTROL_NEXT_BTN;
    btn.classList.add(E.CSS_CLS_ICON_NEXT_BG);
    btn.onclick = this._changeExperience_.bind(this);
    return btn;
  }

  private _createControlRewind15secBtnWrapper() {
    const btn = document.createElement("button");
    btn.ariaLabel = "rewind 15 seconds";
    btn.id = E.CSS_CONTROL_REWIND_15SEC_BTN;
    btn.classList.add(E.CSS_CLS_ICON_REWIND_15SEC_BG);
    btn.onclick = () => this._changeCurrentTime_(null, this._currentTime_ - 15);
    return btn;
  }

  private _createControlForward15secBtnWrapper() {
    const btn = document.createElement("button");
    btn.ariaLabel = "forward 15 seconds";
    btn.id = E.CSS_CONTROL_FORWARD_15SEC_BTN;
    btn.classList.add(E.CSS_CLS_ICON_FORWARD_15SEC_BG);
    btn.onclick = () => this._changeCurrentTime_(null, this._currentTime_ + 15);
    return btn;
  }

  private _createControlVolumeGroup() {
    const wrapper = document.createElement("span");
    const btn = document.createElement("button");
    const range = document.createElement("input");
    wrapper.id = E.CSS_CONTROL_VOLUME_GROUP;
    range.oninput = this._changeVolume_.bind(this);
    btn.id = E.CSS_CONTROL_VOLUME_BTN;
    btn.classList.add(E.CSS_CLS_ICON_VOLUME_BG);
    btn.ariaLabel = "show volume slider";
    btn.onclick = this._toggleMute_.bind(this);
    range.id = E.CSS_CONTROL_VOLUME_RANGE_INPUT;
    range.type = "range";
    range.name = "volume";
    range.min = "0";
    range.max = "1";
    range.step = "0.1";
    range.ariaLabel = "volume slider";
    wrapper.append(btn, range);
    return wrapper;
  }

  private _createControlCaptionGroup() {
    const btn = document.createElement("button");
    btn.id = E.CSS_CONTROL_CAPTION_BTN;
    btn.classList.add(E.CSS_CLS_ICON_CAPTION_BG);
    btn.ariaLabel = "open caption menu";
    btn.setAttribute("aria-expanded", "false");
    btn.setAttribute("aria-controls", E.CSS_CONTROL_POPUP_CONTAINER);
    btn.onclick = this._togglePopupMenu.bind(this);
    return btn;
  }
  private [E.CSS_CONTROL_CAPTION_BTN]() {
    const associateBtn = this.root.getElementById(E.CSS_CONTROL_CAPTION_BTN);
    const ul = document.createElement("ul");
    ul.id = E.CSS_CONTROL_CAPTION_LIST_GROUP;
    const list = [E.CC_OFF, ...this.config.film.tracks.map((t) => t.label)].map(
      (label) => {
        const li = document.createElement("li");
        const a = document.createElement("a");
        const isSelected = associateBtn.dataset.selectedValue === label;
        a.href = "javascript:void 0;";
        a.innerText = label;
        a.dataset.selected = isSelected.toString();
        a.dataset.associateBtnId = associateBtn.id;
        a.onclick = this._changeCaption_.bind(this);
        li.appendChild(a);
        return li;
      }
    );
    ul.append(...list);
    return ul;
  }

  private _createControlPlaybackRateGroup() {
    const btn = document.createElement("button");
    btn.id = E.CSS_CONTROL_PLAYBACK_RATE_BTN;
    btn.ariaLabel = "open playback rate menu";
    btn.innerText = "--";
    btn.setAttribute("aria-expanded", "false");
    btn.setAttribute("aria-controls", E.CSS_CONTROL_POPUP_CONTAINER);
    btn.onclick = this._togglePopupMenu.bind(this);
    return btn;
  }
  private [E.CSS_CONTROL_PLAYBACK_RATE_BTN]() {
    const associateBtn = this.root.getElementById(
      E.CSS_CONTROL_PLAYBACK_RATE_BTN
    );
    const ul = document.createElement("ul");
    ul.id = E.CSS_CONTROL_PLAYBACK_RATE_LIST_GROUP;
    const list = [
      "0.25",
      "0.50",
      "0.75",
      "1.00",
      "1.25",
      "1.50",
      "1.75",
      "2.00",
    ].map((rate) => {
      const li = document.createElement("li");
      const a = document.createElement("a");
      const isSelected = associateBtn.dataset.selectedValue === rate;
      a.href = "javascript:void 0;";
      a.innerText = rate;
      a.dataset.associateBtnId = associateBtn.id;
      a.dataset.selected = isSelected.toString();
      a.onclick = this._changePlaybackRate_.bind(this);
      li.appendChild(a);
      return li;
    });
    ul.append(...list);
    return ul;
  }

  private _createControlFullscreenBtnWrapper() {
    const btn = document.createElement("button");
    btn.ariaLabel = "open in fullscreen";
    btn.id = E.CSS_CONTROL_FULLSCREEN_BTN;
    btn.classList.add(E.CSS_CLS_ICON_FULLSCREEN_BG);
    btn.onclick = this._toggleFullscreen_.bind(this);
    return btn;
  }

  private _createControlCTAGroup() {
    const wrapper = document.createElement("span");
    const space = document.createElement("span");
    wrapper.id = E.CSS_CONTROL_CTA_GROUP;
    space.style.flex = "1";
    space.style.visibility = "hidden";
    wrapper.append(
      this._createControlPreviousBtnWrapper(),
      this._createControlPlayPauseBtnWrapper(),
      this._createControlNextBtnWrapper(),
      this._createControlRewind15secBtnWrapper(),
      this._createControlForward15secBtnWrapper(),
      this._createControlVolumeGroup(),
      space,
      this._createControlCaptionGroup(),
      this._createControlPlaybackRateGroup(),
      this._createControlFullscreenBtnWrapper()
    );
    return wrapper;
  }

  private _createControlPopupList() {
    const container = document.createElement("span");
    const header = document.createElement("span");
    const content = document.createElement("span");
    const closeBtn = document.createElement("button");
    container.id = E.CSS_CONTROL_POPUP_CONTAINER;
    closeBtn.id = E.CSS_CONTROL_CLOSE_POPUP_BTN;
    closeBtn.classList.add(E.CSS_CLS_ICON_CLOSE_BG);
    closeBtn.onclick = this._togglePopupMenu.bind(this);
    content.onclick = this._togglePopupMenu.bind(this);
    header.appendChild(closeBtn);
    container.append(header, content);
    return [container, closeBtn, content];
  }

  private _togglePopupMenu(event: Event) {
    let popup = this.root.getElementById(E.CSS_CONTROL_POPUP_CONTAINER) as any;
    const associateBtn = this.root.getElementById(
      (<any>event.currentTarget).dataset.associateBtnId ||
        (<any>event.currentTarget).id
    );
    if (!popup) {
      const [container, closeBtn, content] = this._createControlPopupList();
      popup = container;
      popup.dataset.associateBtnId = associateBtn.id;
      closeBtn.dataset.associateBtnId = associateBtn.id;
      content.dataset.associateBtnId = associateBtn.id;
      content.appendChild(this[associateBtn.id]());
      associateBtn.setAttribute("aria-expanded", "true");
      associateBtn.after(popup);
      popup.querySelector("a[data-selected='true']").scrollIntoView();
      return;
    }
    if (popup.dataset.associateBtnId === associateBtn.id) {
      popup.remove();
      associateBtn.setAttribute("aria-expanded", "false");
    } else {
      popup.remove();
      const prevAssociateBtn = this.root.getElementById(
        popup.dataset.associateBtnId
      );
      const [container, closeBtn, content] = this._createControlPopupList();
      popup = container;
      popup.dataset.associateBtnId = associateBtn.id;
      closeBtn.dataset.associateBtnId = associateBtn.id;
      content.dataset.associateBtnId = associateBtn.id;
      content.appendChild(this[associateBtn.id]());
      associateBtn.setAttribute("aria-expanded", "true");
      prevAssociateBtn.setAttribute("aria-expanded", "false");
      associateBtn.after(popup);
      popup.querySelector("a[data-selected='true']").scrollIntoView();
    }
  }

  /**
   * Trigger a change in the disabled state
   * or background of a control button to sync
   * with current engine state.
   */
  private _updateControlBtnState() {
    const currentIdx = parseInt(this.getAttribute("conf"));
    const playPauseBtn = this.root.getElementById(
      E.CSS_CONTROL_PLAY_PAUSE_BTN
    ) as any;
    const rewind15SecBtn = this.root.getElementById(
      E.CSS_CONTROL_REWIND_15SEC_BTN
    ) as any;
    const forward15SecBtn = this.root.getElementById(
      E.CSS_CONTROL_FORWARD_15SEC_BTN
    ) as any;
    const prevBtn = this.root.getElementById(E.CSS_CONTROL_PREVIOUS_BTN) as any;
    const nextBtn = this.root.getElementById(E.CSS_CONTROL_NEXT_BTN) as any;
    if (this._isExperiencePaused_) {
      playPauseBtn.classList.remove(E.CSS_CLS_ICON_PAUSE_BG);
      playPauseBtn.classList.add(E.CSS_CLS_ICON_PLAY_BG);
    } else {
      playPauseBtn.classList.remove(E.CSS_CLS_ICON_PLAY_BG);
      playPauseBtn.classList.add(E.CSS_CLS_ICON_PAUSE_BG);
    }
    if (this._currentTime_ > 15) {
      rewind15SecBtn.disabled = false;
    } else {
      rewind15SecBtn.disabled = true;
    }
    if (this._currentTime_ > this._duration_ - 15) {
      forward15SecBtn.disabled = true;
    } else {
      forward15SecBtn.disabled = false;
    }
    if (currentIdx > 0) {
      prevBtn.disabled = false;
    } else {
      prevBtn.disabled = true;
    }
    if (currentIdx < this.configs.length - 1) {
      nextBtn.disabled = false;
    } else {
      nextBtn.disabled = true;
    }
  }

  private _updateControlChapterJump() {
    if (this._currentTime_ > 1 && this._currentTime_ < 3) {
      const progressBar = this.root.getElementById(
        E.CSS_CONTROL_PROGRESS_BAR
      ) as HTMLSpanElement;
      const existingNodes = progressBar.querySelectorAll(
        `.${E.CSS_CLS_CHAPTER_LINK}`
      );
      existingNodes.forEach((node) => node.remove());
      const nodeList: HTMLSpanElement[] = [];
      for (const chapter of this.config.film.chapters) {
        const a = document.createElement("a");
        a.classList.add(E.CSS_CLS_CHAPTER_LINK);
        a.href = "javascript:void 0;";
        a.style.left = `${(chapter / this._duration_) * 100}%`;
        a.onclick = () => this._changeCurrentTime_(null, chapter);
        nodeList.push(a);
      }
      progressBar.append(...nodeList);
    }
  }

  /**
   * Sets needed listeners on the container element
   * and reacts as needed.
   *
   * @param element
   */
  private _addControlListener(control: HTMLElement) {
    const [progressGroup] = control.childNodes as any;
    const [progressBar, timePerDuration] = progressGroup.childNodes as any;
    const [played, buffered] = progressBar.childNodes as any;
    this.addEventListener(E.EVENT_ENGINE_EXPERIENCE_LOADING, () => {
      timePerDuration.innerText = this._timePerDuration_;
      played.style.width = `${progressBar.getAttribute("aria-valuenow")}%`;
      buffered.style.width = `${this._filmBuffered_ * 100}%`;
    });
    this.addEventListener(E.EVENT_ENGINE_TIMEUPDATE, () => {
      const playedAmount = (this._currentTime_ / this._duration_) * 100;
      timePerDuration.innerText = this._timePerDuration_;
      progressBar.setAttribute("aria-valuenow", playedAmount.toString());
      progressBar.setAttribute("aria-valuetext", `${playedAmount}% played`);
      played.style.width = `${progressBar.getAttribute("aria-valuenow")}%`;
      buffered.style.width = `${this._filmBuffered_ * 100}%`;
      this._updateControlBtnState();
      this._updateControlChapterJump();
    });
    this.addEventListener(
      E.EVENT_ENGINE_VISUAL_PRESENTED_TIMEUPDATE,
      ({ detail }: CustomEventInit) => {
        if (detail.currentTime) {
          progressGroup.classList.add(E.CSS_CLS_VISUAL_SHOWING);
        } else {
          progressGroup.classList.remove(E.CSS_CLS_VISUAL_SHOWING);
        }
      }
    );
  }
}
