import { Control } from "./Control";
import { component } from "../util/vendor";
import { E, T } from "../util/constant";

declare var _ive_script_;

interface IVisualProps {}

export class Visual extends Control {
  constructor({}: Partial<IVisualProps> = {}) {
    super();
  }

  protected _addVisualElement() {
    const visual = this._createVisual();
    this._addVisualListener(visual);
    return visual;
  }

  private _createVisual() {
    const container = document.createElement("main");
    container.id = E.CSS_VISUAL_CONTAINER;
    container.classList.add(E.CSS_CLS_HIDDEN);
    container.ontouchend =
      this._toggleControlVisibilityOnTouchDevice_.bind(this);
    container["load"] = (idx: number) => {
      const content = this._createLazyVisualContent(idx);
      try {
        container.replaceChildren(...content);
      } catch (error) {
        container.innerHTML = "";
        container.append(...content);
        console.warn(
          `Compatibility:\nreplaceChildren not supported by browser\n\n${error}`
        );
      }
    };
    return container;
  }

  /**
   * Gets the visual content creation instruction (at index passed as arg) from the data json
   * and renders them. Each element is lazily rendered once only when it is needed.
   * Then once rendered it is kept in memory so that the state will be preserved
   * and when necessary to rerender same element the existing reference in memory
   * will be called and displayed.
   *
   * @param idx
   */
  private _createLazyVisualContent(idx: number) {
    if (!this._visualElements_[idx]) {
      this._visualElements_[idx] = [
        ...this._renderElements(this.config.visuals[idx].elements || []),
        ...this._renderRiots(this.config.visuals[idx].riots || []),
      ];
    }
    return this._visualElements_[idx];
  }

  private _renderElements(
    data: typeof this.config.visuals[number]["elements"]
  ) {
    const elements: HTMLElement[] = [];
    for (const {
      tag,
      id,
      text,
      classList,
      attribute,
      event,
      children,
    } of data) {
      const elem = document.createElement(tag);
      elem.ontouchstart = (e) => e.stopImmediatePropagation();
      elem.ontouchend = (e) => e.stopImmediatePropagation();
      elem.onclick = (e) => e.stopImmediatePropagation();
      if (id) elem.id = id;
      if (text) elem.innerText = text;
      elem.classList.add(...classList);
      for (const key in attribute) {
        elem.setAttribute(key, attribute[key]);
      }
      for (const key in event) {
        elem[key] = (e: Event) => {
          e.stopImmediatePropagation();
          if (_ive_script_ && _ive_script_.hasOwnProperty(event[key])) {
            _ive_script_[event[key]].call(this, e).then((asideIndex: number) =>
              this.dispatchEvent(
                new CustomEvent(E.EVENT_ENGINE_VISUAL_INTERACTION, {
                  detail: { eventType: e.type, asideIndex },
                })
              )
            );
          } else {
            console.warn(
              `Undefined:\n${event[key]} is not a member of _ive_script_`
            );
          }
        };
      }
      // repeat recursively for children
      if (children) {
        elem.append(...this._renderElements(children as any));
      }
      elements.push(elem);
    }
    return elements;
  }

  private _renderRiots(data: typeof this.config.visuals[number]["riots"]) {
    const elements: HTMLElement[] = [];
    for (const { tag, id } of data) {
      if (_ive_script_ && _ive_script_.hasOwnProperty(tag)) {
        const elem = document.createElement("div");
        elem.ontouchstart = (e) => e.stopImmediatePropagation();
        elem.ontouchend = (e) => e.stopImmediatePropagation();
        elem.onclick = (e) => e.stopImmediatePropagation();
        elem.id = id;
        component(_ive_script_[tag])(elem);
        _ive_script_[tag]["exports"]["onInteraction"] = (asideIndex: number) =>
          this.dispatchEvent(
            new CustomEvent(E.EVENT_ENGINE_VISUAL_INTERACTION, {
              detail: { eventType: T.RIOT_INTERACTION, asideIndex },
            })
          );
        elements.push(elem);
      } else {
        console.warn(`Undefined:\n${tag} is not a member of _ive_script_`);
      }
    }
    return elements;
  }

  /**
   * Sets needed listeners on the container element
   * and reacts as needed.
   *
   * @param visual
   */
  private _addVisualListener(visual: HTMLElement) {
    let timerRef;
    let isShowing = false;
    let currentTime = 0;
    let totalTime = 0;
    const config = { attributes: true };
    const observer = new MutationObserver((mutationsList) => {
      for (const mutation of mutationsList) {
        if (mutation.type === "attributes") {
          const currentTarget = <HTMLElement>mutation.target;
          isShowing = !currentTarget.classList.contains(E.CSS_CLS_HIDDEN);
          if (isShowing) {
            currentTime = 0;
            timerRef = setInterval(() => {
              currentTime++;
              this.dispatchEvent(
                new CustomEvent(E.EVENT_ENGINE_VISUAL_PRESENTED_TIMEUPDATE, {
                  detail: { currentTime, totalTime: totalTime || currentTime },
                })
              );
            }, 1000);
          } else {
            totalTime = totalTime + currentTime;
            currentTime = 0;
            clearInterval(timerRef);
          }
          this.dispatchEvent(
            new CustomEvent(E.EVENT_ENGINE_VISUAL_PRESENTED_TIMEUPDATE, {
              detail: { currentTime, totalTime },
            })
          );
        }
      }
    });
    observer.observe(visual, config);
    // observer.disconnect(); // <-- TODO: find best place to clean up
  }
}
